<template>
  <v-textarea
    v-model="valueModel"
    :auto-grow="true"
    :rows="rows"
    :solo="solo"
    :label="label"
    :placeholder="placeholder"
    :outlined="outlined"
    color="success"
    :dark="dark"
    :maxLength="maxLength"
    :success="success"
    :value="valueModel"
    :hide-details="hideDetails"
    @keyup="onKeyUp"
    validate-on-blur
  >
    <template v-slot:prepend-inner>
      <v-icon v-if="icon" class="material-icons-outlined mr-2" dense>{{ icon }}</v-icon>
    </template>
  </v-textarea>
</template>

<script>
export default {
  name: 'TextareaField',
  props: {
    outlined: { default: false },
    label: { required: true },
    placeholder: { default: null },
    value: { default: '' },
    id: { default: null },
    success: { default: null },
    icon: { default: null },
    maxLength: { default: null },
    rows: { default: 1 },
    hideDetails: { default: false },
    solo: { default: false },
    dark: { default: false }, 
  },
  data: () => ({
    valueModel: ''
  }),
  watch: {
    value (newValue) {
      this.valueModel = newValue
    }
  },
  mounted () {
    this.valueModel = this.value
  },
  methods: {
    onKeyUp () {
      this.$emit('onKeyUp', this.valueModel, this.id)
    }
  }
}
</script>