<template>
  <v-row>
    <v-col cols="12" md="7">
      <div class="grey lighten-1 pa-4 step-cart app-border-radius-small">
        <p v-if="services.length > 0">
          Serviço(s):

          <ul>
            <li :key="service.id" v-for="service in services">
              {{ service.name }}
            </li>
          </ul>
        </p>
        <p v-if="barber">
          Barbeiro: <br> {{ barber.name }}
        </p>
        <p v-if="dateTime">
          Horário: <br> {{ formatDateTimeToStringPtBr(dateTime) }}
        </p>
        <p v-if="observation">
          Observação: <br> {{ observation }}
        </p>
        <p class="mb-0">
          <strong>Total: R$ {{ setMoneyMask(price) }}</strong>
        </p>
      </div>
    </v-col>
    <v-col class="mt-0" cols="12" md="5">
      <label class="ma-0">Informação adicional:</label>
      <textarea-field
        class="ma-0 pt-1"
        :label="null"
        placeholder="Ex: Sou alérgico a navalha;"
        @onKeyUp="setObservationText"
      />
    </v-col>
  </v-row>
</template>

<script>
import TextareaField from '@/components/globais/form/textarea-field/TextareaField'
import DateMixin from '@/mixins/DateMixin'
import TextMixin from '@/mixins/TextMixin'

export default {
  name: 'RegisterScheduleStepCart',
  components: { TextareaField },
  mixins: [ DateMixin, TextMixin ],
  props: {
    services: { default: Array },
    barber: { default: null },
    dateTime: { default: null }
  },
  data: () => ({
    observation: null
  }),
  computed: {
    price () {
      let val = this.services.reduce((total, i) => {
        return Number(total) + Number(i.price)
      }, 0)

      return val.toFixed(2)
    }
  },
  methods: {
    setObservationText (text) {
      this.observation = text
      this.$emit('changeObservationText', text)
    }
  }
}
</script>
